import React from 'react';
import { useEffect, useState } from 'react';



const useDinamic = (directionX,directionY, gapX, gapY) =>{
    const [x, setX]=useState({ transform: `translateX(${gapX}px) translateY(${gapY}px)` })
    const [scroll, setScroll]= useState(0)
    useEffect(() => {
      window.addEventListener('scroll', handleScroll);
    }, []);
  
    const handleScroll= function() {
      let scrollTop = window.scrollY
      setScroll(scrollTop)
      let itemTranslateX = scrollTop/2*directionX +gapX
      let itemTranslateY = scrollTop/2*directionY +gapY
      setX({ transform: `translateX(${itemTranslateX}px) translateY(${itemTranslateY}px` })      
      }
    return {x, scroll}
}

export default useDinamic