import React from 'react';
import './App.css';
import Contact from './components/contact';
import DinamicContainer from './components/dinamicContainer';
import Proyects from './components/proyects';
import Tecnologies from './components/technologies';
import useDinamic from "./components/useDinamic"
function App() {
  const title = useDinamic(3,0,60,0).x
  const hr = useDinamic(8,0,0,0).x
  const {scroll} = useDinamic(1,1,1,1)
  const screen = window.screen.width-25
  return (
    <section className="App">
      <header className="App-header">
        <h2 style={title} className="name">Lucas Santillán</h2>
        
      </header>  
      <DinamicContainer/>
      <hr style ={{width:screen-scroll*3+"px"}}/>
      <Tecnologies/>
      <hr style ={{width:screen-scroll+"px"}} id="hrSec"/>
      <Contact/>
      <Proyects/>
    </section>
   
  );
}

export default App;
