import React from "react";
import useDinamic from "./useDinamic";
import ss from "../imgs/Captura.PNG"
export default function Proyects (){
    const {x} = useDinamic(-1,0,1680,-285)
    return <div id="proyects" style ={x}>
        <h6>Bram3d:</h6>
        <section><a href="https://lucassantillan0.github.io/BramedBuild/">
           
            <img src={ss} alt="An example of a corporate website made with React" style={{width:"300px"}}/></a>
            An example of a corporate website made with React
        </section>
    </div>
}